@font-face {
	font-family: "NotoSansRegular";
	src: url(/CarChuno/static/media/NotoSans-Regular.5a87cce8.ttf) format("truetype");
	font-display: swap;
}

@font-face {
	font-family: "NotoSansBold";
	src: url(/CarChuno/static/media/NotoSans-Bold.98f0cacc.ttf) format("truetype");
	font-display: swap;
}

:root {
	--font-family-noto-sans-regular: NotoSansRegular;
	--font-family-noto-sans-bold: NotoSansBold;

	--white: white;
	--white-alternate: #fcfcfc;
	--grey-font-color: rgb(173, 186, 199);
	--black-alternate: #22272e;
	--purple-color: #6c63ff;
	--grey-color: #a8a8a8;
	--grey-2: #373e47;
	--taupe: #815e5b;
	--kombu: #303a2b;
	--dark-green: #297373;
	--filter-1: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

	--primary: var(--taupe);
	--secondary: var(--kombu);
	--font-color: var(--black-alternate);
	--page-background: var(--white-alternate);
	--btn-font-color: var(--white);
	--btn-background-color: var(--primary);
	--btn-shadow: var(--filter-1);
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "sans-serif";
  font-family: var(--font-family-noto-sans-regular, "sans-serif");
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--page-background);
}

@media (prefers-color-scheme: light) {
  :root {
    --page-background: var(--white-alternate);
    --primary: var(--taupe);
    --secondary: var(--kombu);
    --btn-font-color: var(--white-alternate);
    --btn-background-color: var(--primary);
    --btn-shadow: var(--filter-1);
    --font-color: var(--black-alternate);
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    --page-background: var(--black-alternate);
    --primary: var(--white-alternate);
    --secondary: var(--white-alternate);
    --font-color: var(--white);
    --btn-font-color: var(--taupe);
    --btn-background-color: var(--white-alternate);
    --btn-shadow: var(--filter-1);
  }
}

